import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AppComponent } from "./app.component";

import { HeaderComponent } from "./components/frontend/header/header.component";

import { AppRoutingModule } from "./app-routing.module";
import { BackendModule } from "./components/backend/backend.module";
import { ErrorPageComponent } from "./components/error-page/error-page.component";
import { AuthModule } from "./components/auth/auth.module";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { AngularMaterialModule } from "./angular-material";
import { AuthInterceptor } from "./components/auth/auth-interceptor";
import { HTTP_INTERCEPTORS, HttpClientModule } from "@angular/common/http";
import { MasterComponent } from "./components/frontend/master/master.component";
import { FooterComponent } from "./components/frontend/footer/footer.component";
import { NewsComponent } from "./components/frontend/news/news.component";
import { SmartphoneComponent } from "./components/frontend/master/smartphone/smartphone.component";
import { CompareComponent } from "./components/frontend/components/compare/compare.component";

import { SwiperModule } from "ngx-swiper-wrapper";
import { SWIPER_CONFIG } from "ngx-swiper-wrapper";
import { SwiperConfigInterface } from "ngx-swiper-wrapper";


// config swiper
const DEFAULT_SWIPER_CONFIG: SwiperConfigInterface = {
  direction: "horizontal",
  slidesPerView: "auto"
};


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    ErrorPageComponent,
    MasterComponent,
    FooterComponent,
    NewsComponent,
    SmartphoneComponent,
    CompareComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    AuthModule,
    BackendModule,
    FormsModule,
    ReactiveFormsModule,
    SwiperModule,

    BrowserAnimationsModule,
    AngularMaterialModule,
    HttpClientModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },


    {
      provide: SWIPER_CONFIG,
      useValue: DEFAULT_SWIPER_CONFIG
    }

  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
