import { NgModel } from "@angular/forms";
import {
  AfterViewInit,
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  Input,
  ViewEncapsulation
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { ReplaySubject, Subject, Subscription } from "rxjs";
import { take, takeUntil, map } from "rxjs/operators";
import { MatSelect } from "@angular/material";

import { Bank } from "./demo-data";
import { Brand, BrandListModel } from "./brand.model";
import { HttpClient } from "@angular/common/http";

import * as $ from "jquery";

import {
  SwiperComponent,
  SwiperDirective,
  SwiperConfigInterface,
  SwiperScrollbarInterface,
  SwiperPaginationInterface
} from "ngx-swiper-wrapper";

/*
export interface Brand {
  id: string;
  brandName: string;
}
*/

import { environment } from "../../../../../environments/environment";
import { Smartphone } from "./smartphone.model";

import { CompareComponent } from "./../../components/compare/compare.component";
import { ItemCompared } from "./../../components/compare/compare.model";

const BACKEND_URL = environment.apiUrl + "/master";

@Component({
  selector: "app-smartphone",
  templateUrl: "./smartphone.component.html",
  styleUrls: ["./smartphone.component.css"],
  encapsulation: ViewEncapsulation.None
})
export class SmartphoneComponent implements OnInit, AfterViewInit, OnDestroy {
  SmartPhoneSortBys = [
    { key: "New", viewValue: "New" },
    { key: "Popular", viewValue: "Popular" },
    {
      key: "PriceLowestToHighest",
      viewValue: "Lowest price - Highest price"
    },
    { key: "PriceHighestToLowest", viewValue: "Highest price - Lowest price" }
  ];

  getValueSortBy = {
    New: { created_at: -1 },
    Popular: { rating: -1 },
    PriceLowestToHighest: { price: 1 },
    PriceHighestToLowest: { price: -1 }
  };

  dropdownSortSelected = "New";

  dropdownBrand: Brand[] = [];
  state: boolean;
  brandName = "";

  masterSelected = false;

  isLoading = false;

  checkAll = 1;

  /* Begin Compare ****************************************************************************************************************/
  @ViewChild("CompareCom") CompareCom: CompareComponent;

  ItemCompared: ItemCompared[] = [];

  ItemCheckedOld = [];

  /* End Compare ****************************************************************************************************************/

  /* slide ****************************************************************************************************************/

  public show: boolean = true;

  public slides: Smartphone[] = [];

  public type: string = "component";

  public disabled: boolean = false;

  public config: SwiperConfigInterface = {
    a11y: true,
    direction: "horizontal",

    keyboard: true,
    mousewheel: false,
    scrollbar: false,
    navigation: true,
    pagination: false,
    slidesPerView: 5,
    spaceBetween: 30,
    slidesPerGroup: 5,
    loop: false,
    loopFillGroupWithBlank: true,
    breakpoints: {
      640: {
        navigation: false,
        slidesPerView: 1,
        slidesPerGroup: 1
      }
    }
  };

  /* Begin Multiselect ****************************************************************************************************************/

  /** list of BrandList */
  protected BrandList: Brand[] = BrandListModel;

  /** control for the selected bank for multi-selection */
  public bankMultiCtrl: FormControl = new FormControl();

  /** control for the MatSelect filter keyword multi-selection */
  public brandMultiFilterCtrl: FormControl = new FormControl();

  /** list of banks filtered by search keyword */
  public filteredBanksMulti: ReplaySubject<Brand[]> = new ReplaySubject<
    Brand[]
  >(1);

  @ViewChild("multiSelect") multiSelect: MatSelect;

  @Input() clearSearchInput = false;
  @Input() searching = false;

  /** Subject that emits when the component has been destroyed. */
  protected _onDestroy = new Subject<void>();

  /*************************************************************************************************************************/

  constructor(private http: HttpClient) {}

  /*************************************************************************************************************************/

  ngOnInit() {
    this.http
      .get<{ modelList: any }>(BACKEND_URL + "/getSmartphones")
      .pipe(
        // Pipes : A pipe takes in data as input and transforms it to the desired output
        map(modelData => {
          return {
            modelList: modelData.modelList.map(data => {
              return {
                id: data._id,
                brandId: data.brandId._id,
                modelName: data.modelName,
                price: data.price,
                checked: false,
                disabled: false
              }; // array and object
            })
          };
        })
      )
      .subscribe(response => {
        this.slides.push(...response.modelList);
        this.refeshSwiper();
        console.log("modelData = ");
        console.log(response);
      });

    this.http
      .get<{ brandList: any }>(BACKEND_URL + "/getBrand")
      .pipe(
        // Pipes : A pipe takes in data as input and transforms it to the desired output
        map(BrandData => {
          return {
            brandList: BrandData.brandList.map(data => {
              return {
                id: data._id,
                brandName: data.brandName
              }; // array and object
            })
          };
        })
      )
      .subscribe(response => {
        this.BrandList.push({ id: "0", brandName: "All" });
        this.BrandList.push(...response.brandList);

        this.filteredBanksMulti.next(this.BrandList.slice());
        this.dropdownBrand.push(...this.BrandList);
      });

    // set initial selection

    this.bankMultiCtrl.setValue(this.BrandList);

    //this.bankMultiCtrl.setValue([this.BrandList[10], this.BrandList[11], this.BrandList[12]]);

    // load the initial bank list

    // listen for search field value changes
    this.brandMultiFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterBanksMulti();
      });
  } // end ngOninit

  /*************************************************************************************************************************/

  ngAfterViewInit() {
    this.setInitialValue();
  }
  /*************************************************************************************************************************/

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
  }
  /*************************************************************************************************************************/

  /**
   * Sets the initial value after the filteredBanks are loaded initially
   */
  protected setInitialValue() {
    this.filteredBanksMulti
      .pipe(
        take(1),
        takeUntil(this._onDestroy)
      )
      .subscribe(() => {
        // setting the compareWith property to a comparison function
        // triggers initializing the selection according to the initial value of
        // the form control (i.e. _initializeSelection())
        // this needs to be done after the filteredBanks are loaded initially
        // and after the mat-option elements are available
        this.multiSelect.compareWith = (a: Bank, b: Bank) =>
          a && b && a.id === b.id;
      });
  }

  protected filterBanksMulti() {
    if (!this.BrandList) {
      return;
    }
    // get the search keyword
    let search = this.brandMultiFilterCtrl.value;
    if (!search) {
      this.filteredBanksMulti.next(this.BrandList.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    this.filteredBanksMulti.next(
      this.BrandList.filter(
        bank => bank.brandName.toLowerCase().indexOf(search) > -1
      )
    );
  }

  /*************************************************************************************************************************/

  SelectBrandMulti(event, id) {
    var brandSelected = "";

    this.fnGetItemCheckedOld();

    // checkedAll
    if (id == 0 && this.checkAll == 0) {
      this.checkAll = 1;
      this.bankMultiCtrl.setValue(this.BrandList);

      this.dropdownBrand.push(...this.BrandList);

      this.getSmartphone(
        this.dropdownBrand,
        this.getValueSortBy[this.dropdownSortSelected]
      );
    } else if (id == 0 && this.checkAll == 1) {
      this.checkAll = 0;
      this.bankMultiCtrl.setValue([]);
      this.dropdownBrand = [];
      this.slides = [];
    } else {
      setTimeout(() => {
        /* Begin check selection dropdown brand*/

        if (
          event.target.localName === "mat-pseudo-checkbox" ||
          event.target.localName === "span"
        ) {
          console.log(
            "this is checkbox,span = " +
              event.target.offsetParent.attributes[4].nodeValue +
              " id = " +
              id
          );

          if (event.target.offsetParent.attributes[4].nodeValue === "true") {
            this.state = true;
          } else if (
            event.target.offsetParent.attributes[4].nodeValue === "false"
          ) {
            this.state = false;
          }

          this.brandName = event.target.innerText;
        } else if (event.target.localName === "mat-option") {
          console.log(
            "this is mat-option = " +
              event.target.attributes[4].nodeValue +
              " id = " +
              id
          );

          if (event.target.attributes[4].nodeValue === "true") {
            this.state = true;
          } else if (event.target.attributes[4].nodeValue === "false") {
            this.state = false;
          }
        }

        /* End check selection dropdown brand*/

        if (this.state == false) {
          const brandSelected = this.dropdownBrand.filter(x => {
            return x.id !== id;
          });

          this.dropdownBrand = brandSelected;

          const brandSelected2 = this.slides.filter(x => {
            return x.brandId !== id;
          });

          this.slides = brandSelected2;

          this.refeshSwiper();
        } else if (this.state == true) {
          this.dropdownBrand.push({
            id: id,
            brandName: this.brandName
          });

          /*
          const brandSelected2 = this.slides.filter(x => {
            return x.brandId === id;
          });
          */

          this.slides = [];

          /*Begin get item selected from DB*/

          this.getSmartphone(
            this.dropdownBrand,
            this.getValueSortBy[this.dropdownSortSelected]
          );

          /*End get item selected from DB*/
        } // end else true
      }, 0);
    }
  }

  /*************************************************************************************************************************/

  getSmartphone(dropdownBrand, dropdownSortSelected) {
    var WrapDataPost = {
      dropdownBrand,
      dropdownSortSelected
    };

    this.isLoading = true;
    /*Begin get item selected from DB*/

    this.http
      .post<{ modelList: any }>(BACKEND_URL + "/getSmartphone", WrapDataPost)
      .pipe(
        map(modelData => {
          return {
            modelList: modelData.modelList.map(data => {
              console.log(data);
              return {
                id: data._id,
                brandId: data.brandId._id,
                modelName: data.modelName,
                price: data.price,
                checked: false,
                disabled: false
              };
            })
          };
        })
      )
      .subscribe(response => {
        this.slides = response.modelList;

        if (Object.keys(this.ItemCheckedOld).length > 0) {
          this.fnCallItemCheckedOld();
        }

        this.refeshSwiper();
        this.isLoading = false;
      });
  }

  /*************************************************************************************************************************/

  refeshSwiper() {
    this.config = {
      a11y: true,
      direction: "horizontal",

      keyboard: true,
      mousewheel: false,
      scrollbar: false,
      navigation: true,
      pagination: false,
      slidesPerView: 5,
      spaceBetween: 30,
      slidesPerGroup: 5,
      loop: false,
      loopFillGroupWithBlank: true,
      breakpoints: {
        640: {
          navigation: false,
          slidesPerView: 2,
          slidesPerGroup: 2
        },
        1024: {
          navigation: false,
          slidesPerView: 3,
          slidesPerGroup: 3
        }
      }
    };

    this.setCssSwiper();
  }

  /*************************************************************************************************************************/
  checkArrow() {
    var swiperPrev = $(".swiper-button-prev");
    var swiperNext = $(".swiper-button-next");
    if (window.innerWidth <= 1024) {
      console.log("Success", window.innerWidth);
      swiperPrev.css("display", "none");
      swiperNext.css("display", "none");
    } else {
      swiperPrev.css("display", "block");
      swiperNext.css("display", "block");
    }
  }
  /*************************************************************************************************************************/
  SelectSortBy(event) {
    this.dropdownSortSelected = event.value;
    console.log("dropdownSortSelected = " + this.dropdownSortSelected);

    this.fnGetItemCheckedOld();

    this.slides = [];

    this.getSmartphone(
      this.dropdownBrand,
      this.getValueSortBy[this.dropdownSortSelected]
    );
  }

  /************************************************************************************************************/

  fnGetItemCheckedOld() {
    console.log("this.ItemCheckedOld 1 = ");
    console.log(this.ItemCheckedOld);
    for (let index = 0; index < this.slides.length; index++) {
      if (this.slides[index].checked == true) {
        this.ItemCheckedOld[this.slides[index].id] = this.slides[index].id;
      }
    }
  }

  /************************************************************************************************************/

  setCssSwiper() {
    $(document).ready(function() {
      $(".swiper-button-next").css("right", "50%");
      $(".swiper-button-prev").css("left", "50%");

      $(".d-arrow-left").prepend($(".swiper-button-prev"));

      $(".d-arrow-right").prepend($(".swiper-button-next"));
    });
    this.checkArrow();
  }

  /************************************************************************************************************/

  OnAddCompare(event, id, brandId, modelName, price) {
    console.log("id =");
    console.log(id);

    console.log("modelName =");
    console.log(modelName);

    console.log("price =");
    console.log(price);

    console.log("event.checked =");
    console.log(event.checked);

    if (event.checked && this.ItemCompared.length < 4) {
      this.ItemCompared.push(
        ...[
          {
            id: id,
            brandId: brandId,
            modelName: modelName,
            price: price,
            checked: true,
            disabled: false
          }
        ]
      );

      const filter = this.slides.filter(function(x, index) {
        if (x.id == id) {
          x.checked = true;
        }
      });
    } else {
      this.fnUncheckSlide(id);

      const filter = this.ItemCompared.filter(x => {
        return x.id !== id;
      });

      this.ItemCompared = filter;
    }

    if (this.ItemCompared.length == 4) {
      this.fnDisabledCheckbox(true);
    } else {
      this.fnDisabledCheckbox(false);
    }

    console.log(this.ItemCompared);
  }

  /************************************************************************************************************/

  RemoveItemCompared(id) {
    const filter = this.ItemCompared.filter(x => {
      return x.id !== id;
    });

    this.fnUncheckSlide(id);

    this.fnDisabledCheckbox(false);

    this.ItemCompared = filter;
  }

  /************************************************************************************************************/

  fnUncheckSlide(id) {
    console.log("fnUncheckSlide = ");
    console.log(this.slides);

    delete this.ItemCheckedOld[id];

    console.log("fnUncheckSlide 2 = ");
    console.log(this.ItemCheckedOld);

    this.slides.filter(x => {
      if (x.id == id) {
        x.checked = false;
      }
    });
  }

  /************************************************************************************************************/
  fnCallItemCheckedOld() {
    console.log("this.ItemCheckedOld = ");
    console.log(this.ItemCheckedOld);

    for (let index = 0; index < this.slides.length; index++) {
      //  console.log("this.ItemCheckedOld[this.slides[index].id] =");
      //   console.log(this.ItemCheckedOld[this.slides[index].id]);

      if (this.ItemCheckedOld[this.slides[index].id] == this.slides[index].id) {
        this.slides[index].checked = true;
      }
    }

    if (this.ItemCompared.length == 4) {
      this.fnDisabledCheckbox(true);
    } else {
      this.fnDisabledCheckbox(false);
    }
  }
  /************************************************************************************************************/

  fnDisabledCheckbox(statusDisabled) {
    for (let index = 0; index < this.slides.length; index++) {
      if (this.slides[index].checked != true && statusDisabled == true) {
        this.slides[index].disabled = true;
      } else {
        this.slides[index].disabled = false;
      }
    }
  }

  /*************************************************************************************************************************/
}
